.header{
    display: flex;
    align-items: center;
    justify-content: space-around;
    gap: 40px;
    font-weight: 600;
    font-size: large;
}
.box{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: white;
    height: 70px;
    position: fixed;

}
.icons{
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 50px;
}
img{
    height: 70px;
    width: 100px;
}
.logo{
    display: flex;
    flex-direction: row;
}


.icons > div > .button{
    background-color: white;
    border: none;
    color: white;
    padding: 16px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;  
}
.icons > div > .button1{
    background-color:rgb(10, 194, 226);
    color: black; 
    border: 2px solid white;
    border-radius: 30px;
    font-weight: bold;
}
.icons > div > .outer{
    padding-top: 30px;
}
.icons > div > .button1:hover {
    color: white;
  }
  a{
    text-decoration: none;
    color: black;
  }
  .dropdown-content,.dropdown-content1,.dropdown-content2,.dropdown-content3,.dropdown-content4{
    display: none;
    position: absolute;
    text-align: center;
    border-style:inherit;
    padding: 10px;
    background-color: white;
      }
.dropdown-content> a:hover{
    width: 100%;
    text-decoration: underline;
    text-decoration-color: black;
    transform: scale(1.05);
}  
