.box3{
    border-top: solid;
    color: white;
    background-color: black;
    font-size: larger;
    font-weight: 500;
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    margin-top: 20px;
    text-align: center;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 25px;
}
.fa {
    padding: 20px;
    font-size: 30px;
    width: 50px;
    text-align: center;
    text-decoration: none;
    margin: 5px 2px;
    color: white;
  }
  
.fa:hover {
    opacity: 0.7;
}
.fa-phone{
    font-size: 20px;
    margin: -5px -2px;
}
.sponsors{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    gap: 30px;
    flex-wrap: wrap;
  }
  .sponsors > div > img {
    
    height: 150px;
    width: 200px;
  }
  .box1{
    background-color: black;
  }
  .campus{
    display: flex;
    flex-direction: row;
    gap: 30px;
    align-items: baseline;
    flex-wrap: wrap;
  }
  .campus > div > img {
    height: 200px;
    width: 200px;
  }
  .heading{
    padding-top: 20px;
    padding-bottom: 10px;
    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 30px;
    font-weight: bold;
}