.challenges-body{
    background-image: url("space1.jpg");
    background-size: cover;
    display: flex;
    align-items: center;
    justify-content: center;
}
.c_box1{
    padding: 50px;
    text-align: left;
    display: flex;
    flex-direction: row;
    gap: 60px;
    flex-wrap: wrap;
    position:initial;
    justify-content: space-around;
}
.cbody{
    width: 300px;
    background-color: rgb(0, 0, 0, 0.8);
    border-radius: 5%;
    padding: 10px;
    color: white;
}
.cimg{
    height: 200px;
}
.cbody:hover{
    cursor: pointer;
    transform: scale(1.05);
}
h4{
    padding-top: 15px;
}
p{
    padding-top: 10px;
}
.chead{
    font-size: 60px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.head{
    padding-top: 40px;
    color: white;
}